.formBlock {
  background-color: #3b4c56;
  margin: 0 auto;
  max-width: 900px;
  width: 90%;
  padding: 20px;
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.297);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  gap: 40px;

  &__content {
    margin: 0 auto;
    width: 70%;
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  button {
    width: 50%;
    margin: 0 auto;
  }
}

.inputsBlock {
  width: 70%;
  margin: 0 auto;
}
